body {
  background-color: #61dafb;
  padding: 0;
  margin: 0;
  font-family: "Outfit", sans-serif;
}
.main-search {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 12px;
}
.search-bar input {
  padding: 12px;
  width: 500px;
  background: #f5f5f5;
  font-size: 16px;
  color: #ff7800;
  outline: none;
  border-radius: 8px;
  border: 1px solid #f5f5f5;
  box-shadow: 2px 2px 2px #f5f5f5;
  margin-right: 16px;
}
.search-btn {
  padding: 12px;
  outline: none;
  background-color: #ff7800;
  color: #f5f5f5;
  font-weight: bold;
  font-size: 16px;
  border: 1px solid #ff7800;
  border-radius: 8px;
  box-shadow: 2px 1px 2px #ff7800;
  cursor: pointer;
}

.main-card {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 32px;
}
.card-temp {
  width: 620px !important;
  height: 100%;
  background: #f5f5f5 !important;
  font-size: 16px;
  color: #ff7800;
  outline: none;
  border-radius: 8px;
  border: 1px solid #f5f5f5;
  box-shadow: 2px 2px 2px #f5f5f5;
  padding: 1rem;
}
img {
  width: 120px !important;
  height: 120px !important;
}
